html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, strong, em, img, sub, sup, address, cite, pre, code, del, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, figure, figcaption, footer, header, menu, nav, output, section, summary, time, mark, audio, video {
	line-height: 1;
	margin: 0;
	padding: 0;
	border: 0;
	font: inherit;
	font-size: 100%;
	vertical-align: baseline;
}

ol, ul {
	list-style: none;
}

table {
	border-collapse: collapse;
	word-break: break-all;
}

button, html input[type='button'], input[type='reset'], input[type='submit'] {
	overflow: visible;
	-webkit-appearance: button;
	cursor: pointer;
}

input[type='number']::-webkit-inner-spin-button, input[type='number']::-webkit-outer-spin-button {
	height: auto;
}

input[type='search']::-webkit-search-cancel-button, input[type='search']::-webkit-search-decoration {
	-webkit-appearance: none;
}

html {
	overflow-x: hidden;
	width: 100%;
	height: 100%;
	font-size: 62.5%;
	-webkit-user-select: none;
	-webkit-text-size-adjust: none;
	-webkit-tap-highlight-color: transparent;
	-webkit-touch-callout: none;
}

body {
	width: 100%;
	letter-spacing: -.05em;
	word-break: keep-all;
	color: #111;
	background-color: #fff;
	font-family: "ManropeL", "NotoL", serif;
	display: flex;
	flex-flow: column;
	min-height: 100vh;
	color: #2B2B2B;
	font-size: 1.2rem;
}

input, button, select, textarea {
	margin: 0;
	padding: 0;
	border: 0;
	font-family: inherit;
	color: inherit;
	box-sizing: border-box;
	-webkit-appearance: none;
	vertical-align: middle;
	outline: none;
	border-radius: 0;
	background-color: transparent;
	color: #2B2B2B;
	font-size: 1.2rem;
	line-height: 1;
}

caption, legend, hr {
	display: none;
}

em, address, i {
	font-style: normal;
}

img {
	width: 100%;
	vertical-align: middle;
	-webkit-user-select: none;
}

a {
	color: inherit;
	text-decoration: none;
}

strong, b {
	font-weight: normal;
}

a, button, input, img, div, span {
	-webkit-tap-highlight-color: transparent;
	-webkit-touch-callout: none;
	outline: none;
}

@font-face {
	font-family: "NotoL";
	src: url("../font/Noto/notokr-light.eot") format("embedded-opentype"), url("../font/Noto/notokr-light.woff") format("woff"), url("../font/Noto/notokr-light.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "NotoR";
	src: url("../font/Noto/notokr-regular.eot") format("embedded-opentype"), url("../font/Noto/notokr-regular.woff") format("woff"), url("../font/Noto/notokr-regular.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "NotoM";
	src: url("../font/Noto/notokr-medium.eot") format("embedded-opentype"), url("../font/Noto/notokr-medium.woff") format("woff"), url("../font/Noto/notokr-medium.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "NotoB";
	src: url("../font/Noto/notokr-bold.eot") format("embedded-opentype"), url("../font/Noto/notokr-bold.woff") format("woff"), url("../font/Noto/notokr-bold.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "ManropeL";
	src: url("../font/Manrope/Manrope-Light.woff") format("woff"), url("../font/Manrope/Manrope-Light.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "ManropeB";
	src: url("../font/Manrope/Manrope-Bold.woff") format("woff"), url("../font/Manrope/Manrope-Bold.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

html {
	box-sizing: border-box;
	max-height: 99999em;
	padding-bottom: constant(safe-area-inset-bottom);
	padding-bottom: env(safe-area-inset-bottom);
}

.clear {
	content: '';
	display: block;
	clear: both;
}

.hidden {
	position: absolute;
	font: 0/0 a;
	visibility: hidden;
}

.align-c {
	text-align: center;
}

.align-l {
	text-align: left;
}

.align-r {
	text-align: right;
}

.valign-b {
	vertical-align: bottom;
}

.valign-t {
	vertical-align: top;
}

.valign-m {
	vertical-align: middle;
}

.margin-a {
	margin-left: auto;
	margin-right: auto;
}
